.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
/* ::-webkit-scrollbar-track {
  background: "#ff0000";
} */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
/* ::-webkit-scrollbar-thumb:hover {
  background: #555;
} */
hr {
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.t-1 table,
.t-1 th,
.t-1 td {
  border: 1px solid #999;
}
table.t-1 {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
}

.t-1 th {
  border: 1px solid #eee;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #303f9f;
  color: white;
}
table.t-1 td {
  padding-right: 8px;
  padding-left: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #fff;
  color: black;
}

.t-2 table,
.t-2 th,
.t-2 td {
  border: 1px solid #777;
}
table.t-2 {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
}
table.w50 th:first-child {
  width: 50%;
}
table.w40 th:first-child {
  width: 40%;
}
.t-2 th {
  border: 1px solid #777;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #444;
  color: #f1f1f1;
}
table.t-2 td {
  padding-right: 8px;
  padding-left: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #555;
  color: #f1f1f1;
}

a.link-1 {
  color: #dbb027;
}
/* a.link-1:hover {
  cursor: pointer;
} */
details:hover {
  cursor: pointer;
}
.hidden {
  display: none;
}

.MuiGridListTile-root:hover {
  cursor: pointer;
}
.MuiGridListTileBar-root:hover {
  background: rgba(0, 0, 0, 0) !important;
}

a {
  color: #55e986;
}
